
.sprite_login{width:30px;height:30px;display:inline-block;background:url("../assets/svg_sprite.svg") no-repeat;background-position:-5px -55px;vertical-align:-10px
}
.inspirt-nav{position:fixed;z-index:999;top:0;left:0;right:0;min-width:1000px;height:60px;line-height:60px;text-align:center;border-bottom:1px solid #e0e0e0
}
.inspirt-nav>div{float:left
}
.inspirt-nav button{background-color:transparent;border:none
}
.inspirt-nav .logo{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%)
}
.inspirt-nav .inspirt-nav-sidebar{width:60px;height:60px
}
.inspirt-nav .inspirt-nav-r{float:right
}
.inspirt-nav .inspirt-nav-r .inspirt-nav-personal-infoamtion{border-left:1px solid #eee;margin-left:20px
}
.inspirt-nav .inspirt-nav-l{float:left
}
.inspirt-nav .inspirt-nav-l .inspirt-nav-fliter{border-right:1px solid #eee;margin-right:20px
}
.inspirt-nav .inspirt-nav-l div,.inspirt-nav .inspirt-nav-r div{float:left
}
.inspirt-nav .inspirt-nav-search input{border:none;height:60px;margin-left:10px;background-color:transparent
}
.inspirt-nav .inspirt-nav-search input::-webkit-input-placeholder{color:#DCDCDC;font-size:18px
}
.inspirt-nav .inspirt-nav-layout button{margin-left:10px
}



